import React, { useState, useEffect } from 'react'
import Person from './components/Person'
import Form from './components/Form'
import Search from './components/Search'
import personService from './services/persons'
import Notification from './components/Notification'

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState('')
  const [newPhone, setNewPhone] = useState('')
  const [newBusca, setBusca] = useState('')
  const [message, setMessage] = useState(null)
  const [classe, setClasse] = useState(null)

  useEffect(() => {
    personService
      .getAll()
      .then(initialPersons => {
        setPersons(initialPersons)
      })
  }, [])

  const addName = (event) => {
    
      event.preventDefault();
      
      const personObject = {
        name: newName,
        phone: newPhone,
      };

      personService
        .create(personObject)
        .then((returnedPerson) => {
          setPersons(persons.concat(returnedPerson));
          setNewName("");
          setMessage(`Added '${returnedPerson.name}'`);
          setClasse("message");
          setTimeout(() => {
            setMessage(null);
          }, 5000);
        })
        .catch((error) => {
          console.log(error.response.data);
          setMessage(error.response.data.error);
          setClasse(`error`);
          setTimeout(() => {
            setMessage(null);
          }, 5000);
        });    
    // const found = persons.find(person => person.name === newName);
    // event.preventDefault()

    // if (found === undefined){
 
    //   const personObject = {
    //     name: newName,
    //     phone: newPhone,
    //   }

    //   personService
    //     .create(personObject)
    //     .then((returnedPerson) => {
    //       setPersons(persons.concat(returnedPerson));
    //       setNewName("");
    //       setMessage(`Added '${returnedPerson.name}'`);
    //       setClasse("message");
    //       setTimeout(() => {
    //         setMessage(null);
    //       }, 5000);
    //     })
    //     .catch((error) => {
    //       console.log(error.response.data);
    //       setMessage(error.response.data.error);
    //       setClasse(`error`);
    //       setTimeout(() => {
    //         setMessage(null);
    //       }, 5000);
    //     });
    // }else{

    //   const personObject = {
    //     name: newName,
    //     phone: newPhone,
    //   }

    //   personService
    //     .update(found.id, personObject)
    //     .then(returnedPerson => {
    //       setPersons(personsToShow.map(person => person.id !== found.id ? person : returnedPerson))
    //       setMessage(
    //         `Altered '${returnedPerson.name}'`
    //       )
    //       setClasse(
    //         `message`
    //       )
    //       setTimeout(() => {
    //         setMessage(null)
    //       }, 5000)
    //     })
    //     .catch(error => {
    //       setMessage(
    //         `Information of '${personObject.name}' was already removed from server`
    //       )
    //       setClasse(
    //         `error`
    //       )
    //       setTimeout(() => {
    //         setMessage(null)
    //       }, 5000)
    //     })
        
    // }
  }

  const removePerson = id => {
    personService
      .exclui(id)
      .then(returnedPerson => {
        setPersons(personsToShow.filter(n => n.id !== id))
      })
  }

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handlePhoneChange = (event) => {
    setNewPhone(event.target.value)
  }
  const handleBuscaChange = (event) => {
    setBusca(event.target.value)
  }

  
  const personsToShow = newBusca
    ? persons.filter(person => person.name === newBusca) 
    : persons

  
  return (
    <div>
      <div>debug: {newName}</div>
      <div>debug: {newPhone}</div>
      <Search handleBuscaChange={handleBuscaChange} newBusca={newBusca} />
      <Form handleNameChange={handleNameChange} handlePhoneChange={handlePhoneChange} newName={newName} newPhone={newPhone} addName={addName} />
      <h2>Numbers</h2>
      <Notification message={message} classe={classe} />
      <ul>
        {personsToShow.map((person, i) =>
          <Person
            key={i}
            person={person}
            removePerson={() => removePerson(person.id)}
          />
        )}
      </ul>
    </div>
  )
}

export default App


